	body {
		font-family: 'Open sans', sans-serif;
		background-color: #00bcd4;
	}

	#sunrise-sunset-wrapper {
		margin: 20px;
	}

	div.app-fields {
		box-sizing: border-box;
		padding: 1em 0;
		border-radius: 3px;
		color: white;

		font-size: 1.2em;
		text-align: center;
	}

	div.app-fields input {
		background-color: white;
		color: black;
		border-radius: 3px;
		border: none;
		padding: 7px 10px;
	}

	.app-results.active {
		background-color: rgba(0, 0, 0, 0.1);
		padding: 20px;
	}

	div.active {
		margin: 0;
		padding: 0;
		color: #ffffff;
	}

	div.active h2 {
		margin: 0 0 20px 0;
		color: inherit;
	}

	div.ss-result span.label,
	div.active label {
		font-weight: bold;
	}

	div.active .ss-result {
		margin-bottom: 0.5em;
	}

	a.small-link {
		display: inline-block;
		margin-top: 1em;
		font-size: .7em;
		color: inherit;
	}

	strong {
		font-weight: bolder;
	}

	/* Centered display */
	.app-results {
		text-align: center;
	}

	.app-results .ss-result span {
		display: block;
	}